<template>
  <div class="music-list" ref="musiclist">
    <div class="back" @click="back">
      <i class="icon-back"></i>
    </div>
    <h1 class="title" >{{title}}</h1>
    <div class="bg-image" ref="bgImage" :style="bgImgStyle">
      <div class="play-wrapper" @click="startRandomPlay">
        <div ref="playBtn" v-show="songs.length>0" class="play">
          <i class="icon-play"></i>
          <span class="text">随机播放全部</span>
        </div>
      </div>
      <div class="filter" ref="filter"></div>
    </div>
    <div class="bg-layer" ref="layer"></div>
    <scroll :data="songs" @scroll="scroll" :listen-scroll="listenScroll" :probe-type="probeType" class="list" ref="list">
      <div class="song-list-wrapper">
        <song-list :songs="songs" :showRank="showRank" @selectItem="selectItem"></song-list>
      </div>
      <div v-show="!songs.length" class="loading-container">
        <loading></loading>
      </div>
    </scroll>
  </div>
</template>
<script>
import Scroll from 'base/scroll/Scroll'
import Loading from 'base/loading/Loading'
import SongList from 'base/song-list/SongList'
import { prefixStyle } from 'common/js/dom'
import { getRandomArr } from 'common/js/util'
import { mapGetters } from 'vuex'
import { Mixin } from 'common/js/mixin'
const transform = prefixStyle('transform')
//import {playlistMixin} from 'common/js/mixin'
import { mapActions } from 'vuex'
const RESERVED_HEIGHT = 40
export default {
  mixins: [Mixin],
  name: 'MusicList',
  props: {
    bgImage: {
      type: String,
      default: ''
    },
    songs: {
      type: Array,
      default: []
    },
    title: {
      type: String,
      default: ''
    },
    showRank: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      scrollY: 0
    }
  },
  computed: {
    bgImgStyle() {
      return `background-image:url(${this.bgImage})`
    },
    ...mapGetters([
      'sequenceList'
    ])
  },
  created() {
    this.probeType = 3
    this.listenScroll = true
  },
  mounted() {
    let imgHeight = this.$refs.bgImage.clientHeight;
    this.imgHeight = imgHeight;
    this.maxTranslateY = -imgHeight + 40;
    this.$refs.list.$el.style.top = imgHeight + "px"

  },
  methods: {
    scroll(pos) {
      this.scrollY = pos.y
    },
    back() {
      this.$router.back();
    },
    selectItem(item, index) {
      this.selectPlay({
        list: this.songs,
        index
      })
    },
    startRandomPlay() {
      this.setRandomPlay({ list: this.songs });
    },
    handlerMiniPlayer(newPlayList) {
      let bottom = newPlayList.length > 0 ? '60px' : '0px';
      this.$refs.list.$el.style.bottom = bottom;
      this.$refs.list.refresh();

    },
    ...mapActions(['selectPlay', 'setRandomPlay'])
  },
  watch: {
    scrollY(newScrollY) {
      let maxTranslateY = Math.max(this.maxTranslateY, newScrollY)

      this.$refs.layer.style['transform'] = `translate3d(0,${maxTranslateY}px,0)`
      let scale = 1;
      let zIndex = 0;
      if (newScrollY > 0) {
        scale += Math.abs(newScrollY / this.imgHeight)
        zIndex = 10
      }
      if (this.maxTranslateY == maxTranslateY) {
        this.$refs.bgImage.style.paddingTop = 0
        this.$refs.bgImage.style.height = "40px";
        zIndex = 10;
        this.$refs.playBtn.style.display = 'none'
      } else {
        this.$refs.bgImage.style.paddingTop = "70%";
        this.$refs.bgImage.style.height = 0;
        this.$refs.playBtn.style.display = ''
      }
      this.$refs.bgImage.style['transform'] = `scale(${scale})`;
      this.$refs.bgImage.style.zIndex = zIndex;
    }
  },
  components: {
    Loading,
    Scroll,
    SongList
  }
}

</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  @import "~common/stylus/variable"
  @import "~common/stylus/mixin"
  .music-list
    position: fixed
    z-index: 100
    top: 0
    left: 0
    bottom: 0
    right: 0
    background: $color-background
    .back
      position absolute
      top: 0
      left: 6px
      z-index: 50
      .icon-back
        display: block
        padding: 10px
        font-size: $font-size-large-x
        color: $color-theme
    .title
      position: absolute
      top: 0
      left: 10%
      z-index: 40
      width: 80%
      no-wrap()
      text-align: center
      line-height: 40px
      font-size: $font-size-large
      color: $color-text
    .bg-image
      position: relative
      width: 100%
      height: 0
      padding-top: 70%
      transform-origin: top
      background-size: cover
      .play-wrapper
        position: absolute
        bottom: 20px
        z-index: 50
        width: 100%
        .play
          box-sizing: border-box
          width: 135px
          padding: 7px 0
          margin: 0 auto
          text-align: center
          border: 1px solid $color-theme
          color: $color-theme
          border-radius: 100px
          font-size: 0
          .icon-play
            display: inline-block
            vertical-align: middle
            margin-right: 6px
            font-size: $font-size-medium-x
          .text
            display: inline-block
            vertical-align: middle
            font-size: $font-size-small
      .filter
        position: absolute
        top: 0
        left: 0
        width: 100%
        height: 100%
        background: rgba(7, 17, 27, 0.4)
    .bg-layer
      position: relative
      height: 100%
      background: $color-background
    .list
      position: fixed
      top: 0
      bottom: 0
      width: 100%
      background: $color-background
      .song-list-wrapper
        padding: 20px 30px
      .loading-container
        position: absolute
        width: 100%
        top: 50%
        transform: translateY(-50%)
</style>




